<template>
<AppHead :wt="wt">
</AppHead>

<AppLeft :alllinkscontent="alllinkscontent">
</AppLeft>
<div id="bg" class="base_head hnormal"></div>
<AppFoot :wt="wt"></AppFoot>
</template>

<script>
import {
    dqc_rm,
    dqc_ad,
} from '@/utils.js';

import AppLeft from '@/views/AppLeft.vue';
import AppHead from '@/views/AppHead.vue';
import AppFoot from '@/views/AppFoot.vue';

export default {
    name: 'AppBase',
    data() {
        const linkscontent1 = {
                content:  [
                    {class:"icon-zhuye",name:"首页",style:"color:rgb(25, 172, 245);font-size:13px",href:"/",id:1},
                    {class:"icon-wenhao-xianxingyuankuang",name:"关于本站",style:"color:#99cc44;font-size:16px",href:"/article/about",id:2},
                    {class:"icon-fuhaolei-yemian",name:"文章总览",style:"color: rgb(255, 126, 126, 0.845)",href:"/article/view",id:3},
                    {class:"icon-shijian",name:"最近更新",style:"color: rgb(25, 126, 126, 0.845)",id:4,parent:1},
                    {class:"icon-fenlei1",name:"文章分类",style:"color: rgb(25, 26, 126);font-size:14px;padding-left:17.5px",id:5,parent:2},
                ],
                id: 1,
                name: "导航"
        };
    
        const linkscontent2 = {
            content:  [
                {class:"icon-fuhaolei-yemian",name:"资源下载",style:"color:rgb(22, 121, 125);font-size:14px;padding-left:17px",id:6,href:"/data"},
                {class:"icon-xinfengtianchong",name:"联系方式",style:"color:rgb(122, 133, 112);font-size:14px;padding-left:17px",id:0,parent:1},
                {class:"icon-github",name:"github",style:"color:#000000 ",href:"https://github.com/Astesias",id:1,blank:true,sub:1},
                {class:"icon-bilibili-line",name:"bilibili",style:"color:#66ccff ",href:"https://space.bilibili.com/195516167",blank:true,id:2,sub:1},
                {class:"icon-xiangmu2",name:"个人项目",style:"color:rgb(118, 134, 214);font-size:14px;padding-left:17px",id:3,parent:2},
                {class:"icon-gongju-",name:"实用工具",style:"color:rgb(22, 21, 25);font-size:14px;padding-left:17px",id:4,parent:3},
                {class:"icon-lianjie",name:"友情链接",style:"color:rgb(161, 135, 218, 0.904);font-size:14px;padding-left:17px",id:5,parent:4},
            ],
            id: 2,
            name: "关于"
        };
    
        return {
            alllinkscontent: [linkscontent1,linkscontent2],
            wt : this.wt
        }
    },
    components: {
        AppLeft,
        AppHead,
        AppFoot
    },
    methods: {
        resizeListener() {
            this.wt = window.innerWidth > 1000 ? 0 : window.innerWidth > 750 ? 1 : window.innerWidth > 500 ? 2 : 3;
            // console.log(this.wt, window.innerWidth);
            if (this.wt != this.last_wt) {
                location.reload();
                console.log("update");
            }
            this.last_wt = this.wt;
            if (this.wt == 0) {
                dqc_ad(".articleimg-container", "wt0");
            } else {
                dqc_rm(".articleimg-container", "wt0");
            }
        },
    },

    mounted() {
        this.resizeListener();
        window.addEventListener('resize', this.resizeListener);
        
    },
    beforeUnmount() {
        window.removeEventListener('resize', this.resizeListener);
    },
    created() {
        this.last_wt = this.wt = window.innerWidth > 1000 ? 0 : window.innerWidth > 750 ? 1 : window.innerWidth > 500 ? 2 : 3;
    },


}
</script>
